/* Modern Tech Dashboard Styles */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

:root {
  --primary-bg: #0a0a0f;
  --secondary-bg: #1a1a2e;
  --accent-bg: #16213e;
  --glass-bg: rgba(255, 255, 255, 0.05);
  --glass-border: rgba(255, 255, 255, 0.1);
  --neon-blue: #00d4ff;
  --neon-purple: #8b5cf6;
  --neon-green: #00ff88;
  --neon-pink: #ff006e;
  --text-primary: #ffffff;
  --text-secondary: #a0a0a0;
  --text-muted: #666666;
  --shadow-glow: 0 0 20px rgba(0, 212, 255, 0.3);
  --shadow-purple: 0 0 30px rgba(139, 92, 246, 0.4);
  --shadow-green: 0 0 20px rgba(0, 255, 136, 0.3);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

body {
  /* font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif; */
  font-family: Montserrat;
  /* background: var(--primary-bg); */
  /* color: var(--text-primary); */
  overflow-x: hidden;
  min-height: 100vh;
}

/* App Container */
.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, #0a0a0f 0%, #1a1a2e 50%, #16213e 100%);
  position: relative;
}

.App::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 212, 255, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(139, 92, 246, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 40% 60%, rgba(0, 255, 136, 0.05) 0%, transparent 50%);
  pointer-events: none;
  z-index: -1;
  animation: backgroundPulse 8s ease-in-out infinite alternate;
}

/* Enhanced Header */
.header {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  padding: 1rem 0;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  border-bottom: 1px solid var(--glass-border);
  position: sticky;
  top: 0;
  z-index: 1000;
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
}

.logo {
  color: var(--neon-blue);
  font-weight: 700;
  font-size: 1.5rem;
  background: linear-gradient(135deg, var(--neon-blue), var(--neon-purple));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 0 0 20px rgba(0, 212, 255, 0.5);
  position: relative;
}

.logo::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, var(--neon-blue), var(--neon-purple));
  border-radius: 2px;
  animation: logoGlow 2s ease-in-out infinite alternate;
}

@keyframes logoGlow {
  0% { box-shadow: 0 0 5px rgba(0, 212, 255, 0.5); }
  100% { box-shadow: 0 0 20px rgba(139, 92, 246, 0.8); }
}

.nav {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.nav-btn {
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  color: var(--text-secondary);
  font-weight: 500;
  backdrop-filter: blur(10px);
  position: relative;
  overflow: hidden;
  font-family: 'Inter', sans-serif;
}

.nav-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, var(--neon-blue), var(--neon-purple));
  opacity: 0;
  transition: opacity 0.3s ease;
}

.nav-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: var(--neon-blue);
  color: var(--text-primary);
  transform: translateY(-2px);
  box-shadow: var(--shadow-glow);
}

.nav-btn.active {
  background: linear-gradient(135deg, rgba(0, 212, 255, 0.15), rgba(139, 92, 246, 0.15));
  color: var(--text-primary);
  border-color: var(--neon-blue);
  box-shadow: var(--shadow-glow);
}

.nav-btn.active::before {
  opacity: 0.1;
}

.nav-btn:disabled {
  opacity: 0.3;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

/* Main Content */
.main-content {
  flex: 1;
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  width: 100%;
}

/* Enhanced Home Page */
.home-page {
  text-align: center;
  padding: 4rem 2rem;
  /* background: var(--glass-bg); */
  /* backdrop-filter: blur(20px); */
  /* -webkit-backdrop-filter: blur(20px); */
  border: 1px solid var(--glass-border);
  border-radius: 24px;
  margin: 2rem auto;
  max-width: 1100px;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  position: relative;
  overflow: hidden;
}

/* .home-page::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(0, 212, 255, 0.02), rgba(139, 92, 246, 0.02));
  pointer-events: none;
} */

/* .home-page::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 212, 255, 0.1), transparent);
  animation: shimmer 4s infinite;
} */

.home-page h1 {
  font-size: 3.5rem;
  margin-bottom: 1rem;
  font-weight: 700;
  /* background: linear-gradient(135deg, var(--neon-blue), var(--neon-purple), var(--neon-green)); */
  /* -webkit-background-clip: text; */
  /* -webkit-text-fill-color: transparent; */
  /* background-clip: text; */
  animation: textGlow 3s ease-in-out infinite alternate;
  position: relative;
  z-index: 1;
}

.home-page p {
  font-size: 1.3rem;
  margin-bottom: 2.5rem;
  color: var(--text-secondary);
  font-weight: 400;
  line-height: 1.6;
  position: relative;
  z-index: 1;
}

/* Enhanced Buttons */
.btn-primary {
  background: linear-gradient(135deg, var(--neon-blue), var(--neon-purple));
  color: white;
  border: none;
  padding: 1.2rem 2.5rem;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  box-shadow: var(--shadow-glow);
  font-family: Montserrat;
  z-index: 1;
}

.btn-primary::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, var(--neon-purple), var(--neon-blue));
  opacity: 0;
  transition: opacity 0.3s ease;
}

.btn-primary:hover {
  transform: translateY(-4px);
  box-shadow: 
    0 12px 40px rgba(0, 212, 255, 0.4),
    0 0 40px rgba(139, 92, 246, 0.3);
}

.btn-primary:hover::before {
  opacity: 1;
}

.btn-primary:active {
  transform: translateY(-2px);
}

/* Enhanced User Info */
.user-info {
  display: flex;
  align-items: center;
  gap: 1rem;
  background: var(--glass-bg);
  backdrop-filter: blur(10px);
  border: 1px solid var(--glass-border);
  padding: 0.75rem 1.5rem;
  border-radius: 20px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.user-info::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(0, 212, 255, 0.05), rgba(139, 92, 246, 0.05));
  opacity: 0;
  transition: opacity 0.3s ease;
}

.user-info:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: var(--neon-blue);
  box-shadow: 0 4px 16px rgba(0, 212, 255, 0.2);
}

.user-info:hover::before {
  opacity: 1;
}

.rating {
  color: var(--neon-green);
  font-weight: 700;
  font-size: 1.1rem;
  text-shadow: 0 0 10px rgba(0, 255, 136, 0.5);
  position: relative;
  z-index: 1;
}

.rating::before {
  content: '';
  margin-right: 0.25rem;
  animation: starPulse 2s ease-in-out infinite alternate;
}

@keyframes starPulse {
  0% { transform: scale(1); }
  100% { transform: scale(1.1); }
}

/* Animated Background */
.dashboard {
  max-width: 1400px;
  margin: 0 auto;
  padding: 2rem;
  background: linear-gradient(135deg, #0a0a0f 0%, #1a1a2e 50%, #16213e 100%);
  min-height: 100vh;
  position: relative;
}

.dashboard::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 212, 255, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(139, 92, 246, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 40% 60%, rgba(0, 255, 136, 0.05) 0%, transparent 50%);
  pointer-events: none;
  z-index: -1;
  animation: backgroundPulse 8s ease-in-out infinite alternate;
}

@keyframes backgroundPulse {
  0% { opacity: 0.3; }
  100% { opacity: 0.6; }
}

/* Glassmorphism Header */
.dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  padding: 2rem;
  border-radius: 24px;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  position: relative;
  overflow: hidden;
}

.dashboard-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 212, 255, 0.1), transparent);
  animation: shimmer 3s infinite;
}

@keyframes shimmer {
  0% { left: -100%; }
  100% { left: 100%; }
}

.welcome-section h1 {
  font-size: 2.8rem;
  margin-bottom: 0.5rem;
  color: var(--text-primary);
  font-weight: 700;
  letter-spacing: -0.02em;
  background: linear-gradient(135deg, var(--neon-blue), var(--neon-purple));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: textGlow 2s ease-in-out infinite alternate;
}

@keyframes textGlow {
  0% { filter: drop-shadow(0 0 10px rgba(0, 212, 255, 0.5)); }
  100% { filter: drop-shadow(0 0 20px rgba(139, 92, 246, 0.7)); }
}

.highlight {
  color: var(--neon-green);
  font-weight: 700;
  text-shadow: 0 0 10px rgba(0, 255, 136, 0.5);
}

.welcome-section p {
  color: var(--text-secondary);
  font-size: 1.1rem;
  font-weight: 400;
}


/* Modern Tabs */
.dashboard-tabs {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
  overflow-x: auto;
  padding-bottom: 4px;
}

.tab {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem 1.5rem;
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  border-radius: 16px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-weight: 500;
  color: var(--text-secondary);
  backdrop-filter: blur(10px);
  position: relative;
  overflow: hidden;
  white-space: nowrap;
}

.tab::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, var(--neon-blue), var(--neon-purple));
  opacity: 0;
  transition: opacity 0.3s ease;
}

.tab:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-glow);
  border-color: var(--neon-blue);
}

.tab.active {
  background: linear-gradient(135deg, rgba(0, 212, 255, 0.1), rgba(139, 92, 246, 0.1));
  color: var(--text-primary);
  border-color: var(--neon-blue);
  box-shadow: var(--shadow-glow);
}

.tab.active::before {
  opacity: 0.1;
}

/* Enhanced Stats Cards */
.quick-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.stat-card {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 2rem;
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  border-radius: 20px;
  box-shadow: 
    0 4px 20px rgba(0, 0, 0, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.stat-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, var(--neon-blue), var(--neon-purple), var(--neon-green));
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-6px);
  box-shadow: 
    0 12px 40px rgba(0, 0, 0, 0.3),
    var(--shadow-glow);
  border-color: var(--neon-blue);
}

.stat-card:hover::before {
  transform: scaleX(1);
}

.stat-icon {
  width: 60px;
  height: 60px;
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, var(--neon-blue), var(--neon-purple));
  box-shadow: var(--shadow-glow);
  animation: iconPulse 2s ease-in-out infinite alternate;
}

@keyframes iconPulse {
  0% { transform: scale(1); }
  100% { transform: scale(1.05); }
}

.stat-content {
  flex: 1;
}

.stat-value {
  font-size: 2.2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.25rem;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.2);
}

.stat-label {
  color: var(--text-secondary);
  font-size: 0.95rem;
  margin-bottom: 0.25rem;
  font-weight: 500;
}

.stat-change {
  color: var(--neon-green);
  font-size: 0.85rem;
  font-weight: 600;
  text-shadow: 0 0 8px rgba(0, 255, 136, 0.3);
}

/* Enhanced Charts */


/* Enhanced Skill Chart */
.skill-radar-chart {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.skill-legend h4 {
  margin-bottom: 1rem;
  color: var(--text-primary);
  font-weight: 600;
}

.skill-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.skill-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.02);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  transition: all 0.3s ease;
}

.skill-item:hover {
  background: rgba(255, 255, 255, 0.05);
  border-color: var(--neon-blue);
  box-shadow: 0 4px 12px rgba(0, 212, 255, 0.1);
}

.skill-name {
  min-width: 90px;
  font-size: 0.95rem;
  color: var(--text-secondary);
  font-weight: 500;
}

.skill-bar {
  flex: 1;
  height: 10px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  overflow: hidden;
  position: relative;
}

.skill-progress {
  height: 100%;
  background: linear-gradient(90deg, var(--neon-blue), var(--neon-purple), var(--neon-green));
  border-radius: 6px;
  transition: width 0.8s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  box-shadow: 0 0 10px rgba(0, 212, 255, 0.3);
}

.skill-progress::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  animation: progressShimmer 2s infinite;
}

@keyframes progressShimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

.skill-percentage {
  min-width: 50px;
  text-align: right;
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--text-primary);
  text-shadow: 0 0 8px rgba(255, 255, 255, 0.2);
}

/* Enhanced Performance Chart */
.performance-chart {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  border-radius: 24px;
  padding: 2.5rem;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.chart-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.chart-controls h2 {
  color: var(--text-primary);
  font-weight: 600;
  background: linear-gradient(135deg, var(--neon-blue), var(--neon-purple));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.chart-tabs {
  display: flex;
  gap: 0.5rem;
}

.chart-tabs button {
  padding: 0.75rem 1.25rem;
  border: 1px solid var(--glass-border);
  background: var(--glass-bg);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.9rem;
  color: var(--text-secondary);
  font-weight: 500;
  backdrop-filter: blur(10px);
}

.chart-tabs button:hover {
  background: rgba(255, 255, 255, 0.05);
  border-color: var(--neon-blue);
  color: var(--text-primary);
}

.chart-tabs button.active {
  background: linear-gradient(135deg, var(--neon-blue), var(--neon-purple));
  color: white;
  border-color: transparent;
  box-shadow: var(--shadow-glow);
}

/* Enhanced Insights */
.performance-insights {
  margin-top: 2rem;
  padding-top: 2rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.performance-insights h3 {
  margin-bottom: 1.5rem;
  color: var(--text-primary);
  font-weight: 600;
  background: linear-gradient(135deg, var(--neon-blue), var(--neon-purple));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.insights-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

.insight-card {
  padding: 2rem;
  background: rgba(255, 255, 255, 0.02);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-left: 4px solid var(--neon-blue);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.insight-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(0, 212, 255, 0.02), rgba(139, 92, 246, 0.02));
  opacity: 0;
  transition: opacity 0.3s ease;
}

.insight-card:hover {
  background: rgba(255, 255, 255, 0.05);
  border-color: var(--neon-blue);
  box-shadow: 0 8px 24px rgba(0, 212, 255, 0.1);
  transform: translateY(-2px);
}

.insight-card:hover::before {
  opacity: 1;
}

.insight-card h4 {
  margin-bottom: 1rem;
  color: var(--text-primary);
  font-weight: 600;
}

.insight-card ul {
  list-style: none;
  padding: 0;
}

.insight-card li {
  padding: 0.5rem 0;
  color: var(--text-secondary);
  font-weight: 400;
  position: relative;
  padding-left: 1.5rem;
}

.insight-card li::before {
  content: '';
  position: absolute;
  left: 0;
  color: var(--neon-blue);
  font-weight: 600;
}

/* Enhanced Loading States */
.dashboard-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 50vh;
  color: var(--text-primary);
}

.loading-spinner {
  width: 50px;
  height: 50px;
  border: 3px solid rgba(255, 255, 255, 0.1);
  border-top: 3px solid var(--neon-blue);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
  box-shadow: 0 0 20px rgba(0, 212, 255, 0.3);
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.chart-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 200px;
  color: var(--text-secondary);
}

.chart-placeholder {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 200px;
  color: var(--text-secondary);
  text-align: center;
  background: rgba(255, 255, 255, 0.02);
  border-radius: 12px;
  border: 1px dashed rgba(255, 255, 255, 0.1);
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, var(--neon-blue), var(--neon-purple));
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, var(--neon-purple), var(--neon-blue));
}

/* Enhanced Responsive Design */
@media (max-width: 768px) {
  .dashboard {
    padding: 1rem;
  }
  
  .dashboard-header {
    flex-direction: column;
    gap: 1.5rem;
    text-align: center;
    padding: 1.5rem;
  }
  
  .welcome-section h1 {
    font-size: 2.2rem;
  }
  

  
  .dashboard-tabs {
    flex-wrap: wrap;
    gap: 0.75rem;
  }
  
  .charts-grid {
    grid-template-columns: 1fr;
  }
  
  .quick-stats {
    grid-template-columns: 1fr;
  }
  
  .insights-grid {
    grid-template-columns: 1fr;
  }
  
  .stat-card {
    padding: 1.5rem;
  }
  
  .chart-card {
    padding: 1.5rem;
  }
  
  .chart-controls {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }
}

/* Animation for elements entering viewport */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.stat-card,
.chart-card,
.insight-card {
  animation: fadeInUp 0.6s ease-out;
}

/* Additional tech-inspired effects */
.tech-border {
  position: relative;
}

.tech-border::before,
.tech-border::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  border: 2px solid var(--neon-blue);
}

.tech-border::before {
  top: -1px;
  left: -1px;
  border-right: none;
  border-bottom: none;
}

.tech-border::after {
  bottom: -1px;
  right: -1px;
  border-left: none;
  border-top: none;
}

/* Data visualization enhancements */
.data-point {
  position: relative;
  display: inline-block;
}

.data-point::after {
  content: '';
  position: absolute;
  width: 4px;
  height: 4px;
  background: var(--neon-blue);
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 8px var(--neon-blue);
  animation: pulse 2s ease-in-out infinite alternate;
}

@keyframes pulse {
  0% { opacity: 0.5; transform: translate(-50%, -50%) scale(1); }
  100% { opacity: 1; transform: translate(-50%, -50%) scale(1.2); }
}